header {
  background: inherit;
  
  height: 60px;
  max-width: 1550px;
  width: 93%;

  display: flex;
  justify-content: space-between;
  align-items: center;

  position: absolute;
  left: 50%;
  top: 30px;
  transform: translate(-50%, -50%);
  z-index: 99;

  .brand, .link--item {
    font-size: 1rem;
    font-weight: bold;
  }

  .brand:hover {
    color: var(--main-color)
  }

  .link--item {
    padding: 4px 12px;
    &.active, &:hover {
      color: var(--second-color);
      transition: all 0.25s linear;
    }
  }
}
