.projects {
  .projects--nav--list {
    margin-top: 50px;
    margin-bottom: 20px;
    li {
      background: var(--sec-bg-color);
      border-radius: 2px;
      cursor: pointer;
      display: inline-block;
      padding: 7px 15px;
      margin-bottom: 10px;
      transition: 250ms;
      overflow: hidden;
      &:not(:last-child) {
        margin-right: 5px;
      }
      &.active {
        background: var(--second-color);
        color: var(--main-color);
      }
      &:hover {
        background-color: var(--second-color-dark);
      }
    }
  }

  .project-wrapper {
    margin-bottom: 40px;

    .mix {
      padding: 5px;

      .project {
        background: var(--sec-bg-color);
        border: 10px solid var(--sec-bg-color);
        border-radius: 2px;
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        overflow: hidden;
        &:hover {
          .info {
            opacity: 1;
          }
        }
        .info {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: 100%;
          height: 100%;
          padding: 10px 5px;
          span.section {
            color: var(--second-color);
            font-size: 14px;
            font-weight: bold;
          }
          h3.title {
            font-size: 24px;
            margin: 10px 0;
          }
          p.desc {
            color: #b5bbbf;
            font-size: 16px;
          }
          .button {
            text-align: center;
            width: 100%;
          }
        }
      }
    }
  }
}