.contact {
  .form-section {
    background: var(--sec-bg-color);
    border-radius: 2px;
    margin-top: 40px;
    margin-bottom: 40px;
    padding-bottom: 40px;
    form {
      padding: 30px 15px;
      overflow: hidden;
      max-width: 1200px;
      margin: auto;
      h3 {
        margin-bottom: 30px;
      }
      .form-group {
        position: relative;
        .form-control {
          background: var(--main-color);
          border-radius: 2px;
          font-size: 15px;
          padding-bottom: 3px;
          height: 46px;
          transition: 250ms;
          outline: none;
          box-shadow: none;
          &:focus {
            border: 1px solid var(--second-color);
          }
          &:focus + label {
            position: absolute;
            top: 2px;
            left: 5px;
            color: var(--second-color);
            font-size: 11px;
          }
        }
        label {
          color: var(--dark-color);
          font-size: 14px;
          transition: 250ms;
          position: absolute;
          top: 12px;
          left: 10px;
          svg {
            margin-right: 5px;
          }
        }
        textarea {
          resize: none;
          height: 108px !important;
          padding: 15px;
        }
        button {
          background: var(--second-color);
          color: var(--main-color);
          width: 100%;
          border: none;

          &::before {
            border-color: var(--second-color);
          }
          .icon {
            margin-left: 5px;
          }
        }
        &.label-data {
          label {
            position: absolute;
            top: 2px;
            left: 5px;
            color: var(--dark-color);
            font-size: 11px;
          }
        }
      }
    }

    .con-info {
      flex-wrap: wrap;
      padding: 0 20px;
      margin-top: 30px;
      .contact-info {
        .addr {
          transition: 250ms;
          svg {
            margin: auto;
            display: block;
            text-align: center;
            font-size: 20px;
          }
        }

        &:hover .addr {
          color: var(--second-color);
          font-weight: bold;
        }
      }
    }

    .social-icons {
      margin: 30px auto 0;
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;
      max-width: 350px;
      a {
        background-color: var(--main-color);
        border: 3px solid var(--main-color);
        border-radius: 50%;
        font-size: 22px;
        padding: 8px;
        margin: 10px;
        transition: 0.3s;
        display: flex;
        justify-content: center;
        align-items: center;
        // Colors
        $social: (
          facebook: #3b5998,
          behance: #131418,
          linkedin: #0077b5,
          github: #6e5494,
          youtube: #ff0000,
        );
        @each $name, $color in $social {
          &.#{$name} {
            color: $color;
            &:hover {
              border: 3px solid $color;
            }
          }
        }
      }
    }
  }
}
