.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;

  .avatar {
    background: none !important;
    border: 2px solid var(--second-color);
    border-radius: 50% !important;
    width: 11rem !important;
    height: 11rem !important;
    padding: 0 !important;
    margin: 0 auto 25px !important;
    overflow: hidden;
    position: relative;
    &::after {
        content: "";
        background-color: rgba(var(--main-bg-color-rgba), .3);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 4;
        transition: 250ms linear;
    }
    &:hover {
        &::after{
            background-color: rgba(var(--main-bg-color-rgba), .0);
        }
    }
  }

  .bio--links {
    display: flex;
    justify-content: center;
    
      .button {
          margin: 5px 8px;
          font-weight: bold;
          display: flex;
          align-items: center;
          &.btn--primary:hover {
              background: inherit
          }
          
          .link--icon {
              margin-right: 5px;
              font-weight: bold;
          }
      }
  }
}