.button {
  padding: 8px 20px;
  border-radius: 2px;
  cursor: pointer;
  border: none;
  outline: none;

  &.btn--primary {
    background-color: var(--second-color);
    border: 1px solid var(--second-color);
    color: var(--main-color);
  }

  &.btn--outline {
    background: transparent;
    border: 1px solid var(--second-color);
    color: var(--main-color);
  }

  &.btn--meduim {
    padding: 8px 20px;
    font-size: 16px;
  }

  &.btn--large {
    padding: 12px 26px;
    font-size: 16px;
  }

  &.btn--meduim:hover,
  &.btn--large:hover {
    background-color: var(--second-color-dark);
    transition: 250ms;
  }
}