.about {
  .row {
    text-align: left;
    border-left: 1px solid var(--second-color);
    padding-left: 30px;
    margin: 50px 0 40px 15px;

    .about-me {
      background: var(--sec-bg-color);
      border-bottom: 1px solid var(--second-color);
      border-radius: 2px;
      color: var(--main-color);
      width: 100%;
      margin-bottom: 50px;
      padding: 10px;
      position: relative;

      &::before {
        background-color: var(--second-color);
        position: absolute;
        content: "";
        width: 0;
        height: 3px;
        left: 0;
        bottom: 0;
        transition-duration: 0.3s;
      }

      &:hover {
        &::before {
          width: 100%;
        }
      }

      h3 {
        color: var(--main-color);
        position: relative;

        svg {
          position: absolute;
          color: var(--second-color);
          left: -57px;
          font-size: 2rem;
        }
      }

      h4 {
        svg {
          color: var(--second-color);
          margin-right: 8px;
          margin-top: -3px;
        }
      }

      ul {
        border-left: 1px solid var(--second-color);
      }
    }
  }
}
