:root {
  --main-bg-color: #1e202a;
  --sec-bg-color: #3f4853;
  --main-color: #ebebeb;
  --second-color: #11a3e7;
  --second-color-dark: #088cc9;
  --dark-color: rgba(34, 34, 34);

  --main-bg-color-rgba: 45, 57, 78;

  --main-font: "Ubuntu", sans-serif;
}

.App {
  background-color: var(--main-bg-color);
  color: var(--main-color);
  font-family: var(--main-font);
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}
.about,
.projects,
.contact {
  padding-top: 120px;
  min-height: 100%;
  max-width: 1550px;
  width: 93%;
  margin: auto;
}

li,
p {
  font-size: 16px;
  color: #b5bbbf;
  line-height: 1.7;
}

a {
  color: var(--main-color);
  cursor: pointer;
  text-decoration: none !important;
  outline: 0;
}

span.colored {
  color: var(--second-color);
}

h2 {
  font-size: 32px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 20px;
}

h1,
h2,
h3,
h4 {
  color: var(--main-color);
  font-weight: bold;
}

h2 {
  margin-bottom: 20px;
  position: relative;
  span {
    color: var(--second-color);
  }
}

h2 ~ p {
  color: #b5bbbf;
  width: 70%;
  margin: 0 auto;
  @media (max-width: 768px) {
    width: 100%;
  }
}
